.table {
  width: 100%;
  padding: 20px;
  border-right: 1px solid grey;
  background-color: white;
  .row {
    display: grid;
    grid-template-columns: 10% 10% 40% 10% 10%;
    grid-gap: 5px;
    .cell {
      padding: 5px;
      background-color: rgb(227, 219, 219);
      border: solid 1px grey;
      box-sizing: border-box;
    }
  }
}

.mannage-panel {
  width: 100%;
  height: 50px;
  display: flex;
  width: 100%;
}
.mannage-panel-btn {
  width: 100px;
  height: 90%;
  padding: 10px 20px;
  color: $white-text-color;
  text-align: center;
  line-height: 20px;
  font-size: 17px;
  font-weight: 400;
  cursor: pointer;
}
